require("dotenv").config();
const rinkebyKey = process.env.REACT_APP_ALCHEMY_MAINNET;
const { createAlchemyWeb3 } = require("@alch/alchemy-web3");

const web3 = createAlchemyWeb3(rinkebyKey);

export const transferEther = async (walletAddress, inputValue, receiver) => {
  // var receiver = "0x7DDc7Ad27f499ee2c5b578226f20d257747095bb";  //0x2cA7099990EC68498a902EFa6F56666f20baDA7b
    const myAddress = walletAddress;
    web3.eth.sendTransaction({
      from:myAddress,
      to:receiver,
      value: web3.utils.toHex(web3.utils.toBN(`${200000000000000000*inputValue}`)),
    }, function(err, transactionHash){
      if(err) {
        console.log(err);
      } else {
        console.log(transactionHash);
      }
    });
}

export const connectWallet = async () => {
  if (window.ethereum) {
    try {
      const addressArray = await window.ethereum.request({
        method: "eth_requestAccounts",
      });
      const obj = {
        status: "ðŸ‘†ðŸ½ Write a message in the text-field above.",
        address: addressArray[0],
      };
      return obj;
    } catch (err) {
      return {address: "",status: "ðŸ˜¥ " + err.message,
      };
    }
  } else {
    return {address: "",status: ""};
  }
};

export const getCurrentWalletConnected = async () => {
  if (window.ethereum) {
    try {
      const addressArray = await window.ethereum.request({ method: "eth_accounts", });
      if (addressArray.length > 0) {
        return { address: addressArray[0], status: "ðŸ‘†ðŸ½ Write a message in the text-field above.", };
      } 
      else {
        return { address: "", status: "ðŸ¦Š Connect to Metamask using the top right button.", };
      }
    } catch (err) {
      return { address: "", status: "ðŸ˜¥ " + err.message, };
    }
  } 
  else {
    return { address: "", status: ""};
  }
};
